import * as types from './mutation-types'
export default {
  addTotalTime ({ dispatch }, time) {
    dispatch(types.ADD_TOTAL_TIME, time)
  },
  decTotalTime ({ dispatch }, time) {
    dispatch(types.DEC_TOTAL_TIME, time)
  },
  savePlan ({ dispatch }, plan) {
    dispatch(types.SAVE_PLAN, plan)
  },
  deletePlan ({ dispatch }, plan) {
    dispatch(types.DELETE_PLAN, plan)
  }
}



